.mfp-bg {
  background: rgba(8, 47, 79, .8); }

.popup {
  max-width: 405px;
  margin: auto;
  &__head {
    display: flex;
    padding-bottom: 10px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  &__title {
    padding-left: 25px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(30px);
    color: white;
    @include bp(375px) {
      @include fz(18px);
      padding-left: 5px; } }
  &__close {
    cursor: pointer;
    transition: all .3s ease;
    &:hover,
    &:focus {
      opacity: .8; } }
  &__close-text {
    position: relative;
    font-family: $cBoldFont;
    @include fz(18px);
    @include lh(30px);
    color: white; }
  &__cross {
    margin-left: 10px;
    @include fz(18px);
    @include lh(30px);
    color: #fff; }
  &__block {
    padding: 25px 30px 20px;
    background: white;
    @include bp(375px) {
      padding: 5px; } }
  &__form {
    width: 100%; }
  &__hr {
    height: 1px;
    margin: 20px auto;
    border: 0;
    background: #d1d1d1; }
  &__text {
    font-family: $regularFont;
    @include fz(20px);
    @include lh(24px);
    color: black;
    &--strong {
      font-family: $cBoldFont;
      font-weight: normal; }
    &--min {
      padding: 10px 40px 20px;
      font-family: $mainFont;
      @include fz(12px);
      @include lh(18px); }
    &--cost {
      text-align: right;
      font-family: $lightFont;
      @include fz(15px);
      @include lh(20px);
      color: $darkGrey;
      br {
        @include bp(375px) {
          display: none; } } }

    &--accent {
      font-family: $mainFont;
      text-align: right;
      @include fz(14px);
      @include lh(20px);
      color: $formBlue;
      br {
        @include bp(375px) {
          display: none; } } }
    &--sum {
      text-align: center;
      position: relative;
      @include fz(40px);
      @include lh(60px);
      color: $darkGrey;
      white-space: nowrap;
      &:after {
        @include fz(38px);
        margin-left: -10px;
        content: '₽'; } }
    &--tip,
    &--subj {
      display: inline-block;
      font-family: $mainFont;
      @include fz(14px);
      @include lh(24px);
      color: $darkGrey; }
    &--subj {
      color: $red; } }
  &__tit {
    margin-bottom: 20px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(30px); }
  &__checkbox {
    width: 100%;
    display: block;
    cursor: pointer;
    position: relative;
    padding-left: 37px;
    @include fz(20px);
    @include lh(30px);
    font-family: $mainFont;
    color: $darkGrey;
    transition: all .3s ease;
    &--min {
      @include fz(12px);
      @include lh(18px); }
    &:hover,
    &:focus {
      color: $hrefColor; }
    &:before,
    &:after {
      position: absolute;
      display: block;
      content: '';
      width: 20px;
      height: 20px;
      background: $blue;
      top: 5px;
      left: 0; }
    &:after {
      transition: all .3s ease;
      display: none;
      background: $blue url(../img/check.png) center no-repeat; } }
  input[type="checkbox"] {
    display: none;
    &:checked + .popup__checkbox:after {
      display: block; } }
  &__input {
    width: 100%;
    height: 40px;
    padding-left: 15px;
    font-family: $mainFont;
    @include fz(15px);
    @include lh(40px);
    color: $darkGrey;
    border: 1px solid $mainAccent;
    &--email,
    &--password,
    &--confirm {
      margin-bottom: 20px; }
    &--password,
    &--confirm {
      width: 100%;
      margin-right: 15px; } }
  &__price {
    display: flex;
    width: 100%;
    padding-top: 20px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @include bp(375px) {
      flex-direction: column; } }
  &__tip,
  &__confirmed {
    display: inline-block;
    @include bp(375px) {
      padding-left: 15px;
      margin: 0 auto 20px; } }
  &__icon {
    margin-right: 5px;
    @include fz(20px);
    @include lh(24px);
    &--red {
      color: $red; } }
  &__link {
    display: inline-block;
    font-family: $mainFont;
    @include fz(13px);
    @include lh(30px);
    color: $mainAccent;
    transition: all .3s ease;
    &:hover,
    &:focus {
      color: $hrefColor; }
    &--center {
      width: 100%;
      margin-top: 20px;
      text-align: center; }
    + .popup__link--center {
      margin-top: 5px; } }
  &__btn {
    width: 100%;
    height: 40px;
    margin-top: 20px;
    text-align: center;
    border: none;
    background: $green;
    font-family: $mainFont;
    @include fz(16px);
    @include lh(40px);
    transition: all .3s ease;
    color: white;
    &:hover,
    &:focus {
      background: $blue; }
    &--blue {
      background: $blue;
      &:hover,
      &:focus {
        background: $green; } } }
  &--register {
    .popup__input {
      &--password,
      &--confirm {
        width: 55%;
        max-width: 195px;
        @include bp(375px) {
          width: 100%;
          max-width: 100%; } } } }
  &--auth {
    .attent-text {
      margin-top: 0; } }
  &__select {
    display: block;
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    padding: 0 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: 1px solid $mainAccent; }
  &__service {
    max-width: 355px;
    height: 40px;
    overflow-y: scroll; }
  .tabs {
    width: 100%;
    &__controls {
      width: 100%; }
    &__content {
      height: 160px; }
    &__tab {
      border-radius: 10px;
      text-align: center;
      width: 168px;
      height: 43px;
      margin-bottom: 20px;
      font-family: $regularFont;
      @include fz(15px);
      @include lh(43px);
      color: $darkGrey;
      transition: all .3s ease;
      border: 1px solid #d1d1d1;
      &.active,
      &:hover,
      &:focus,
      &:active {
        border: 1px solid transparent;
        color: white;
        background: $blue; } }
    &__form {
      padding: 0;
      background: white;
      input[type="text"] {
        padding-left: 15px;
        height: 40px;
        margin-bottom: 18px;
        @include fz(16px);
        @include lh(40px);
        border: 1px solid $mainAccent;
        background: transparent; } }
    &__inputNumbers {
      padding: 0; }
    &__example,
    &__knowhow {
      width: 100%;
      margin-bottom: 18px;
      color: $darkGrey;
      @include bp(1250px) {
        text-align: center;
        margin-bottom: 25px; }
      @include bp(600px) {
        display: inline-block; } }
    &__knowhow {
      display: inline-block;
      font-family: $mainFont;
      @include fz(13px);
      @include lh(30px);
      color: $mainAccent;
      transition: all .3s ease;
      &:hover,
      &:focus {
        color: $hrefColor; } } } }
