.package {
  width: 100%;
  position: relative;
  &__items {
    width: 100%;
    min-height: 450px;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    @include bp(1024px) {
      justify-content: center;
      min-height: 385px; } }
  &__item {
    position: relative;
    flex-shrink: 0;
    width: calc((100% - 100px)/6);
    min-height: 345px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: white;
    opacity: 1;
    transition: all .3s ease;
    visibility: visible;
    &.hide {
      opacity: 0;
      visibility: hidden; }
    @include bp(1024px) {
      width: calc((100% - 40px)/3);
      margin: 0 auto 20px; }
    @include bp(800px) {
      width: calc((100% - 40px)/3); }
    @include bp(600px) {
      min-height: 320px;
      width: calc((100% - 20px)/2); }
    @include bp(480px) {
      width: 90%; }
    &--hit {
      min-height: 385px;
      @include bp(1024px) {
        min-height: 345px; }
      @include bp(600px) {
        min-height: 320px; } }
    &--add {
      justify-content: center;
      margin-right: 0;
      background: $mainAccent;
      border: 2px dashed white;
      @include bp(1024px) {
        min-height: 345px;
        margin-right: auto; }
      @include bp(600px) {
        min-height: 320px; } } }

  &__count {
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    font-family: $cBoldFont;
    @include fz(18px);
    @include lh(44px);
    color: $mainAccent;
    border-bottom: 1px solid #1577a4; }
  &__label {
    padding: 2px 10px;
    position: absolute;
    top: 45px;
    right: 0;
    font-family: $cBoldFont;
    @include fz(18px);
    text-transform: uppercase;
    color: white;
    border-radius: 0 0 0 10px;
    background: $red; }
  &__price {
    flex-shrink: 0;
    margin: 20px auto;
    position: relative;
    display: block;
    width: 140px;
    height: 140px;
    color: white;
    text-align: center;
    font-family: $cBoldFont;
    @include fz(55px);
    @include lh(140px);
    background: $mainAccent;
    border-radius: 50%;
    @include bp(600px) {
      width: 100px;
      height: 100px;
      @include fz(40px);
      @include lh(100px); }
    &:after {
      margin-left: -10px;
      @include fz(48px);
      content: '₽';
      @include bp(600px) {
        @include fz(34px); } }
    &--add {
      margin: 30px auto;
      font-family: $cLightFont;
      @include fz(134px);
      @include lh(120px);
      background: $blue;
      border: 3px dotted white;
      cursor: pointer;
      transition: all .3s ease;
      @include bp(600px) {
        @include fz(100px);
        @include lh(84px); }
      &:hover,
      &:focus,
      &:active {
        background: $inputBlue;
        border: 3px dotted $inputBlue; }
      &:after {
        display: none; } }
    &--hit {
      background: $green; } }
  &__cost {
    padding-right: 9px;
    text-align: center;
    position: relative;
    font-family: $cBoldFont;
    @include fz(18px);
    color: $darkGrey;
    &:after {
      content: '₽';
      position: absolute;
      top: 0;
      right: 0;
      font-family: $cBoldFont;
      font-weight: bold;
      @include fz(16px);
      @include lh(24px); } }
  &__time {
    width: 60%;
    text-align: center;
    font-family: $lightFont;
    @include fz(16px);
    color: $darkGrey;
    &--hit {
      position: relative;
      top: -10px; }
    @include bp(1024px) {
      top: 0; }
    @include bp(600px) {
      top: -5px; } }
  &__text {
    width: 80%;
    text-align: center;
    font-family: $cBoldFont;
    @include fz(18px);
    color: white; } }
.create {
  display: none;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: -10px;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: $mainAccent;
  transition: all .3s ease;
  @include bp(1024px) {
    top: auto;
    bottom: 0; }
  &.active {
    display: flex;
    opacity: 1;
    visibility: visible; }
  &__close {
    align-self: flex-end;
    margin-bottom: 10px;
    vertical-align: middle;
    cursor: pointer; }
  &__close-text {
    font-family: $cBoldFont;
    @include fz(18px);
    @include lh(25px);
    color: #fff;
    transition: all .3s ease;
    &:hover,
    &:focus,
    &:active {
      .create__cross {
        color: $hrefColor; } } }
  &__cross {
    margin-left: 10px;
    @include fz(18px);
    @include lh(25px);
    color: #fff;
    transition: all .3s ease;
    &:hover,
    &:focus,
    &:active {
      color: $hrefColor; } }
  &__form {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 55px 25px 75px;
    background: $mainAccent;
    border: 2px dashed white;
    @include bp(800px) {
      padding: 30px 25px; }
    @include bp(600px) {
      padding: 15px; } }
  &__inputs {
    display: flex;
    width: 100%;
    min-height: 140px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    @include bp(800px) {
      flex-direction: column; } }
  &__enter {
    width: 50%;
    max-width: 460px;
    text-align: right;
    @include bp(800px) {
      width: 100%;
      max-width: 600px;
      text-align: center;
      display: flex;
      justify-content: space-between;
      align-items: flex-start; }
    @include bp(600px) {
      flex-direction: column; } }
  &__field {
    @include bp(600px) {
      width: 100%; } }
  &__label {
    margin-right: 20px;
    font-family: $cBoldFont;
    text-transform: uppercase;
    @include fz(18px);
    color: white;
    @include bp(1024px) {
      display: inline-block;
      width: 245px;
      text-align: left;
      margin-bottom: 10px; }
    @include bp(800px) {
      width: 200px; }
    @include bp(600px) {
      width: 100%;
      margin: 0 auto 5px;
      @include fz(14px); } }
  &__number,
  &__time {
    width: 280px;
    height: 40px;
    padding: 0 15px;
    background: $inputBlue;
    border: none;
    @include bp(800px) {
      width: 220px; }
    @include bp(600px) {
      width: 100%; } }
  &__number {
    margin-bottom: 20px; }
  &__time,
  &__variant {
    font-family: $mainFont;
    @include fz(16px);
    @include lh(44px);
    color: black; }
  &__time {
    background: $inputBlue url('../img/select-arr.png') right 5px center no-repeat; }

  &__equal {
    margin: 0 60px;
    font-family: $cBoldFont;
    @include fz(100px);
    @include lh(100px);
    color: white;
    @include bp(800px) {
      @include fz(40px);
      @include lh(60px); }
    @include bp(600px) {
      display: none; } }
  &__circles {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    @include bp(600px) {
      margin-top: 20px; } }
  &__price,
  &__total {
    flex-shrink: 0;
    width: 140px;
    height: 140px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: white;
    border-radius: 50%;
    @include bp(1024px) {
      width: 100px;
      height: 100px; } }
  &__total {
    margin-left: 20px;
    background: $mainAccent;
    border: 2px solid white; }
  &__value {
    position: relative;
    &:after {
      content: '₽'; }
    &--price {
      font-family: $cBoldFont;
      @include fz(55px);
      @include lh(55px);
      color: #3b464f;
      @include bp(1024px) {
        @include fz(40px);
        @include lh(40px); }
      &:after {
        @include fz(48px);
        @include lh(55px);
        margin-left: -10px;
        @include bp(1024px) {
          @include fz(35px);
          @include lh(40px); } } }
    &--total {
      font-family: $regularFont;
      @include fz(30px);
      color: white;
      @include bp(1024px) {
        @include fz(20px); }
      &:after {
        margin-left: -5px; } } }
  &__remark {
    font-family: $mainFont;
    @include fz(14px);
    color: $mainAccent;
    @include bp(1024px) {
      @include fz(12px); }
    &--total {
      color: white; } }
  &__profit {
    margin-top: 50px;
    font-family: $regularFont;
    @include fz(30px);
    color: white;
    @include bp(800px) {
      margin-top: 10px;
      margin-bottom: 10px; } }
  .btn--main {
    position: absolute;
    bottom: 0;
    transform: translateY(50%); } }


.btn {
  border: none;
  border-radius: 10px;
  background: $green;
  font-family: $semiFont;
  color: white;
  transition: all .3s ease;
  &:hover,
  &:focus,
  &:active {
    background: $blue; }
  &--buy {
    width: 145px;
    height: 33px;
    margin: 18px auto;
    @include fz(16px);
    @include lh(33px);
    @include bp(600px) {
      width: auto;
      padding: 0 15px; } }
  &--main {
    width: 285px;
    height: 65px;
    font-family: $semiFont;
    @include fz(26px);
    @include bp(800px) {
      height: 40px;
      @include fz(18px); } } }
