.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  color: #fff;
  &__topLine {
    width: 100%;
    background: $darkGrey;
    @include fz(13px);
    a {
      display: block;
      margin-left: 20px;
      font-family: $lightFont;
      transition: all .3s ease;
      color: #fff;
      &:hover,
      &:active,
      &:focus {
        color: $green; } }
    @include bp(800px) {
      display: none; } }
  &__container {
    position: relative;
    @include container(); }
  &__title {
    padding-left: 20px;
    font-family: $lightFont;
    @include fz(13px);
    @include lh(30px); }
  &__right {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    @include bp(1024px) {
      width: 80%; } }
  &__how {
    border-bottom: 1px dashed #fff;
    &:hover,
    &:focus,
    &:active {
      color: $mainBgColor;
      border-bottom: 1px dashed transparent; } }
  &__questions {
    &:hover,
    &:focus,
    &:active {
      color: $mainBgColor; } }
  &__view {
    position: relative;
    padding: 0 23px 0 68px;
    text-align: center;
    @include fz(15px);
    @include lh(30px);
    background: #000 url(../img/eye.png) 35px center no-repeat;
    &:hover,
    &:focus,
    &:active {
      color: $mainBgColor; }
    @include bp(1250px) {
      padding: 0 15px 0 40px;
      background: #000 url(../img/eye.png) 10px center no-repeat; }
    @include bp(1024px) {
      padding: 0 10px 0 30px;
      background-size: 15px auto; } }
  &__bottomLine {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: flex-start;
    @include bp(800px) {
      flex-wrap: wrap; } }
  &__logo {
    flex-shrink: 0;
    width: 280px;
    height: 90px;
    padding: 20px 20px 0;
    background: white;
    border-radius: 0 0 20px 0;
    @include bp(1250px) {
      width: 200px;
      height: 70px; }
    @include bp(1024px) {
      width: 150px;
      height: 40px;
      padding-top: 5px; }
    @include bp(800px) {
      order: 2; } }

  &__img {
    display: block;
    width: 100%;
    object-fit: contain; }
  &__nav {
    position: relative;
    width: 100%;
    min-height: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: stretch;
    align-items: center;
    background: $mainAccent;
    @include bp(1250px) {
      min-height: 35px; }
    a {
      display: block;
      text-align: center;
      font-family: $lightFont;
      @include fz(16px);
      @include lh(60px);
      color: $mainBgColor;
      border-left: 1px solid $border;
      transition: all .3s ease;
      &:hover,
      &:active,
      &:focus {
        color: $mainFontColor; }
      @include bp(1250px) {
        @include lh(35px); }
      @include bp(1024px) {
        @include fz(12px); }
      @include bp(600px) {
        @include fz(14px);
        border-bottom: 1px solid $border; }
      @include bp(480px) {
        @include fz(12px); }
      &.header__home {
        @include bp(600px) {
          display: none; } } } }
  &__cabinet {
    flex-shrink: 0;
    max-width: 220px;
    padding: 0 23px 0 68px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: url(../img/key.png) left 40px center no-repeat;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:focus {
      background: url(../img/key--grey.png) left 40px center no-repeat;
      @include bp(1250px) {
        background: url(../img/key--grey.png) left 10px center no-repeat; }
      @include bp(1024px) {
        background-size: 10px auto; } }
    @include bp(1250px) {
      padding: 0 15px 0 40px;
      background: url(../img/key.png) left 10px center no-repeat; }
    @include bp(1024px) {
      padding: 0 10px 0 30px;
      background-size: 10px auto; } }
  &__sale,
  &__package,
  &__service {
    flex-shrink: 0;
    padding: 0 23px;
    @include bp(1250px) {
      padding: 0 15px; }
    @include bp(1024px) {
      padding: 0 10px; } }
  &__package {
    @include bp(600px) {
      padding: 0 30px;
      width: 50%; }
    @include bp(480px) {
      padding: 0 5px; } }
  &__service,
  &__sale,
  &__cabinet {
    @include bp(600px) {
      width: 50%; } }
  &__home {
    width: 90px;
    height: 60px;
    background: url(../img/home.png) center no-repeat;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:focus {
      background: url(../img/home--grey.png) center no-repeat; }
    @include bp(1250px) {
      width: 50px;
      height: 35px; }
    @include bp(800px) {
      width: 35px; } } }


.dropdown {
  position: absolute;
  display: flex;
  top: 70px;
  left: 395px;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  max-width: 460px;
  padding: 5px 25px;
  border-radius: 10px;
  background: white;
  box-shadow: 13px 13px 38px 0 rgba(0, 0, 0, .37);
  z-index: 500;
  opacity: 0;
  transition: .25s;
  visibility: hidden;
  @include bp(1250px) {
    top: 45px;
    left: 270px; }
  @include bp(1024px) {
    left: 200px; }
  @include bp(800px) {
    max-width: 300px;
    left: 50px;
    padding: 5px 10px; }
  @include bp(480px) {
    left: 10px; }
  &.active {
    visibility: visible;
    opacity: 1;
    top: 60px; }
  &:before {
    position: absolute;
    top: -12px;
    left: 70px;
    display: block;
    content: '';
    width: 25px;
    height: 25px;
    background: white;
    transform: rotate(45deg); }
  &__items {
    list-style-type: none;
    padding: 0; }
  &__link {
    display: block;
    font-family: $lightFont;
    @include fz(14px);
    @include lh(20px);
    color: $formBlue;
    border: none;
    transition: all .3s ease;
    margin-bottom: 10px;
    @include bp(800px) {
      @include lh(18px); }
    &:hover,
    &:active,
    &:focus {
      color: $green; }
    &--new {
      font-family: $mainFont;
      color: $green;
      &:hover,
      &:active,
      &:focus {
        color: $blue; } } } }
