.content_paket {
  width: 100%;
  padding: 65px 0 70px;
  &__container {
    @include container();
    flex-direction: column; }
  .package {
    @include bp(1250px) {
      margin-bottom: 50px; }
    &__item {
      border: 1px solid #d1d1d1;
      &--add {
        border: 2px dashed white; } }
    &__time {
      width: 62%; } }
  .create {
    background: $mainBgColor;
    &__close {
      &:hover,
      &:active,
      &:focus {
        .create__cross {
          color: $green; } } }
    &__close-text {
      color: $mainAccent; }
    &__cross {
      color: $mainAccent;
      transition: all .3s ease;
      &:hover,
      &:active,
      &:focus {
        color: $green; } } } }


.attent-text {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 55px;
  padding: 15px 20px 20px 15px;
  background: #ffe6c4;
  &__icon {
    margin-right: 15px;
    @include fz(20px);
    @include lh(24px);
    color: #ff5a16; }
  &__text {
    font-family: $mainFont;
    @include fz(16px);
    @include lh(24px);
    color: black; } }
