.benefit {
  width: 100%;
  padding: 50px 0 60px;
  background: $mainAccent;
  &__container {
    @include container();
    flex-direction: column;
    align-items: center; }
  &__title {
    margin-bottom: 35px;
    font-family: $cBoldFont;
    @include fz(40px);
    color: #fefefe;
    @include bp(600px) {
      @include fz(30px); }
    @include bp(480px) {
      text-align: center; } } }
