.why {
  position: relative;
  width: 100%;
  padding: 50px 0 125px;
  border-bottom: 10px solid $green;
  &__container {
    @include container();
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }
  &__title {
    margin-bottom: 50px;
    font-family: $cBoldFont;
    @include fz(40px);
    color: $darkGrey;
    @include bp(800px) {
      text-align: center; }
    @include bp(600px) {
      @include fz(34px); } }
  &__items {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(800px) {
      flex-wrap: wrap; } }
  &__item {
    width: 25%;
    min-width: 150px;
    @include bp(800px) {
      width: 50%;
      margin-bottom: 20px; }
    @include bp(480px) {
      width: 90%;
      margin: 0 auto 15px; } }
  &__num {
    font-family: $cBoldFont;
    @include fz(90px);
    color: $blue;
    @include bp(1250px) {
      @include fz(60px); }
    @include bp(800px) {
      text-align: center; } }
  &__small {
    @include fz(50px);
    @include bp(1250px) {
      @include fz(40px); } }
  &__text {
    font-family: $lightFont;
    @include fz(22px);
    color: $darkGrey;
    @include bp(1250px) {
      @include fz(18px); }
    @include bp(800px) {
      text-align: center; } }
  &__banner {
    position: absolute;
    bottom: 0;
    transform: translateY(50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 780px;
    min-height: 145px;
    padding: 20px 25px 30px;
    background: white;
    border: 3px dashed $blue;
    z-index: 10;
    @include bp(1024px) {
      width: 60%;
      min-width: 280px;
      padding: 15px; }
    @include bp(600px) {
      width: 90%; } }
  &__img {
    width: 212px;
    height: 49px;
    margin-right: 30px;
    object-fit: contain;
    @include bp(1024px) {
      width: 100px;
      height: auto; }
    @include bp(800px) {
      display: none; } }
  &__tit {
    margin-bottom: 15px;
    font-family: $cBoldFont;
    @include fz(30px);
    color: $blue; }
  &__descr {
    font-family: $lightFont;
    @include fz(16px);
    color: $darkGrey; } }
