.payment {
  width: 100%;
  padding: 50px 0 40px;
  background: #fff;
  @include bp(600px) {
    padding: 40px 0 30px; }
  &__container {
    @include container(center);
    flex-direction: column;
    align-items: flex-start; }
  &__title {
    font-family: $cBoldFont;
    color: $darkGrey;
    @include fz(40px);
    @include bp(600px) {
      @include fz(34px); } }
  &__means {
    width: 100%;
    margin-top: 35px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include bp(600px) {
      margin-top: 25px; }
    @include bp(480px) {
      justify-content: center; } }
  &__mean {
    @include bp(1024px) {
      width: 30%;
      margin-bottom: 20px; }
    @include bp(480px) {
      width: 50%;
      padding: 0 5px; } }
  &__img {
    @include bp(1024px) {
      display: block;
      margin: 0 auto; } } }
