.samples {
  width: 100%;
  padding-top: 35px;
  padding-bottom: 35px;
  &__container {
    @include container(flex-end);
    flex-direction: column;
    align-items: flex-start; }
  &__title {
    margin-bottom: 15px;
    font-family: $cBoldFont;
    @include fz(40px);
    @include lh(40px);
    color: $darkGrey;
    @include bp(600px) {
      @include fz(34px); } }
  &__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch; }
  &__item {
    width: calc((100% - 60px)/4);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    @include bp(1024px) {
      width: calc((100% - 40px)/3); }
    @include bp(600px) {
      width: 100%; }
    &--attention {
      padding: 30px 40px 30px 25px;
      background: $green;
      @include bp(1024px) {
        width: 100%;
        margin-top: 20px; } } }
  &__image-zoom {
    @include bp(600px) {
      display: block;
      width: 100%; } }
  &__img {
    width: 280px;
    height: 400px;
    object-fit: contain;
    @include bp(1250px) {
      width: 210px;
      height: 300px; }
    @include bp(800px) {
      width: 150px;
      height: auto; }
    @include bp(600px) {
      display: block;
      margin: 0 auto; } }

  &__text {
    margin: 20px 0;
    font-family: $mainFont;
    font-weight: normal;
    @include fz(14px);
    color: $mainAccent; }

  &__tit {
    font-family: $boldFont;
    @include fz(18px);
    color: white;
    text-transform: uppercase; }
  &__message {
    margin-top: 20px;
    font-family: $lightFont;
    @include fz(13px);
    @include lh(20px);
    color: white; }
  &__link {
    font-family: $boldFont;
    color: white;
    transition: all .3s ease;
    &:hover,
    &:focus,
    &:active {
      border-bottom: 1px dashed white; } }
  .dashlist {
    @include bp(600px) {
      margin-bottom: 25px; } } }
.mfp-close {
  &:hover,
  &:focus,
  &:active {
    background: transparent; } }
