.info {
  width: 100%;
  padding: 75px 0 55px;
  background: url(../img/info__bcg.jpg) top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  @include bp(480px) {
    padding-top: 40px; }
  &--price {
    padding-top: 200px;
    @include bp(1250px) {
      padding-top: 60px; } }

  &__container {
    @include container();
    flex-direction: column;
    justify-content: flex-start;
    align-items: space-between; }
  &__top {
    width: 100%;
    margin-bottom: 35px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @include bp(800px) {
      flex-direction: column;
      align-items: flex-start; }
    .btn {
      width: 280px;
      height: 42px;
      @include fz(16px);
      @include lh(42px);
      border-radius: 5px;
      @include bp(1024px) {
        flex-shrink: 0;
        width: auto;
        padding: 0 15px; }
      @include bp(800px) {
        margin-top: 20px;
        margin-left: 15px; } } }
  &__title {
    font-family: $cBoldFont;
    @include fz(40px);
    @include lh(42px);
    color: white;
    @include bp(800px) {
      padding-left: 15px; }
    @include bp(600px) {
      @include fz(34px); }
    @include bp(480px) {
      @include fz(28px);
      @include lh(40px); } }
  &__controls {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    @include bp(800px) {
      display: block;
      margin-bottom: 10px; } }

  &__tabs {
    margin-bottom: 20px; }
  &__tab {
    width: calc((100% - 60px)/4);
    min-height: 107px;
    padding: 30px 25px 20px;
    border-radius: 20px 20px 0 0;
    transition: all .3s ease;
    cursor: pointer;
    @include bp(1250px) {
      padding: 15px;
      min-height: 90px; }
    @include bp(1024px) {
      padding: 15px 10px;
      min-height: 90px; }
    @include bp(800px) {
      width: 100%;
      border-radius: 5px;
      min-height: 30px;
      padding: 5px 15px; }
    &--price {
      width: calc((100% - 40px)/3);
      @include bp(1024px) {
        min-height: 70px; }
      @include bp(800px) {
        width: 100%;
        min-height: 30px; } }
    &.active,
    &:hover,
    &:focus,
    &:active {
      background: white;
      .info__subtitle {
        color: $darkGrey; } } }
  &__subtitle {
    font-family: $semiFont;
    @include fz(18px);
    @include lh(28px);
    color: white;
    transition: all .3s ease;
    @include bp(1250px) {
      @include fz(16px); }
    @include bp(1024px) {
      @include fz(14px);
      @include lh(22px); }
    @include bp(800px) {
      @include fz(16px);
      @include lh(30px); } }
  &__content {
    width: 100%;
    min-height: 450px;
    padding: 50px 30px 25px;
    background: white;
    @include bp(800px) {
      padding: 20px; }
    @include bp(480px) {
      padding: 20px 10px; }
    &--ergn {
      min-height: 615px; }

    &--price {
      @include bp(1250px) {
        min-height: 480px; }
      @include bp(1024px) {
        padding: 20px; }
      @include bp(800px) {
        min-height: 583px; } } }
  &__item {
    width: 100%;
    display: none;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(800px) {
      flex-direction: column; }
    &.active {
      display: flex; } }
  &__textwrap {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    @include bp(1250px) {
      flex-wrap: wrap; } }
  &__text {
    font-family: $mainFont;
    @include fz(16px);
    @include lh(24px);
    color: $darkGrey;
    &--pb {
      padding-bottom: 30px; }
    + .info__text {
      margin-top: 30px; }
    &--strong {
      font-family: $semiFont;
      font-weight: normal; } }
  &__features {
    flex-shrink: 0;
    width: 55%;
    margin-right: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(1024px) {
      width: 100%;
      margin-bottom: 20px; } }
  &__feature {
    width: 40%; }
  &__logos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    width: 100%; }
  &__logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: calc((100% - 60px)/4);
    padding: 25px;
    border: 1px solid #d1d1d1;
    @include bp(800px) {
      padding: 10px; }
    @include bp(480px) {
      padding: 5px; } }
  &__img {
    width: 100%;
    height: auto;
    object-fit: contain;
    @include bp(1250px) {
      max-width: 150px; } }
  .attent-text {
    position: relative;
    flex-shrink: 0;
    width: 25%;
    max-width: 250px;
    margin-top: 0;
    margin-left: 20px;

    @include bp(1024px) {
      width: 30%;
      min-width: 215px; }
    @include bp(800px) {
      width: 100%;
      max-width: 600px;
      margin-left: 0;
      margin-top: 20px; }
    &--label {
      margin-top: 100px; }
    &__text {
      width: 100%;
      font-family: $mainFont;
      @include fz(14px);
      @include lh(22px);
      color: black; } }
  &__about {
    width: 100%;
    padding: 50px 30px 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background: white;
    @include bp(480px) {
      padding: 25px 10px; } }
  &__tit {
    margin-bottom: 40px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(30px);
    color: $darkGrey;
    @include bp(800px) {
      margin-bottom: 25px; }
    &--big {
      @include fz(40px);
      line-height: auto;
      @include bp(1024px) {
        @include fz(28px); } } }
  &__columns {
    margin-top: 30px;
    margin-bottom: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(1024px) {
      flex-wrap: wrap; } }
  &__column {
    &--left {
      width: 65%;
      @include bp(1024px) {
        padding: 0; } }
    &--right {
      width: 35%;
      padding: 0 20px;
      @include bp(1024px) {
        padding: 20px 0 0; } }
    &--right,
    &--left {
      @include bp(1250px) {
        width: 50%; }
      @include bp(1024px) {
        width: 100%; } } } }
.twocol {
  &--ergn {
    @include bp(1250px) {
      margin-top: 0;
      flex-wrap: wrap; } }
  &--min {
    max-width: 850px;
    @include bp(800px) {
      flex-direction: column; } }
  &--sample {
    @include bp(1024px) {
      flex-direction: column; } } }
.label--top {
  position: absolute;
  top: -100px;
  width: 156px;
  height: 73px;
  padding-left: 25px;
  display: block;
  text-align: center;
  color: $darkGrey;
  font-family: $cBoldFont;
  @include fz(35px);
  @include lh(50px);
  background: url(../img/label.png) center no-repeat;
  background-size: contain;
  z-index: 30;
  &:after {
    content: '₽';
    margin-left: -5px;
    @include fz(30px); } }


.listitem {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  list-style-type: none;
  &--ergn {
    flex-direction: column;
    @include bp(1250px) {
      margin: 0; } }
  &__point {
    position: relative;
    width: 300px;
    margin-right: 25px;
    padding-bottom: 20px;
    font-family: $lightFont;
    @include fz(16px);
    @include lh(35px);
    @include bp(600px) {
      margin-right: 0; }
    @include bp(480px) {
      width: 100%; }
    &--long {
      width: 100%;
      max-width: 400px; }
    &:before {
      position: absolute;
      left: -45px;
      display: block;
      content: '';
      width: 35px;
      height: 35px;
      background: url(../img/point.jpg) center no-repeat; } } }

.numlist {
  display: block;
  width: 100%;
  padding-bottom: 20px;
  padding-left: 15px;
  &__text {
    padding-left: 15px;
    font-family: $mainFont;
    @include fz(16px);
    @include lh(24px);
    color: $darkGrey; } }
