@charset "UTF-8";
@import "common/fonts";
@import "common/var";
@import "common/libs";
@import "common/mixin";
@import "common/fonts";
@import "common/layout";
@import "common/custom";

// Общие стили проекта
html {
  line-height: 1.2;
  font-size: $mainFontSize;
  height: 100%; }

body {
  position: relative;
  display: flex;
  min-height: 100%;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  font-family: $mainFont;
  background: $mainBgColor;
  overflow-X: hidden; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

p {
  margin: 0;
  color: $mainFontColor; }

a {
  text-decoration: none;
  color: $hrefColor; }

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle; }

textarea {
  resize: none; }

input,
select,
button {
  outline: none; }

*, *:before, *:after {
  box-sizing: border-box; }

.hide {
  display: none; }

.show {
  display: block; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

section {
  position: relative; }

.wrapper {
  flex: 1 0 auto;
  min-height: 100%;
  background: transparent; }

fieldset {
  border: 0;
  padding: 0;
  margin: 0; }

select {
  -webkit-appearance: none;
  background-image: url('../img/select-arr.png');
  background-position: right 5px center;
  background-repeat: no-repeat;
  @include lh(40px);
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: '';
  -ms-appearance: none;
  appearance: none!important; }


select::-ms-expand {
  display: none; }

::-webkit-input-placeholder {
  color: black;
  font-family: $mainFont;
  @include fz(16px);
  @include lh(40px); }
::-moz-placeholder {
  color: black;
  font-family: $mainFont;
  @include fz(16px);
  @include lh(40px); }

input[type="text"]:focus::-webkit-input-placeholder,
input[type="password"]:focus::-webkit-input-placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  -o-transition:	opacity 0.5s ease;
  transition:	opacity 0.5s ease; }
input[type="text"]:focus::-moz-placeholder,
input[type="password"]:focus::-moz-placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  -o-transition:	opacity 0.5s ease;
  transition:	opacity 0.5s ease; }
input[type="text"]:focus:-moz-placeholder,
input[type="password"]:focus:-moz-placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  -o-transition:	opacity 0.5s ease;
  transition:	opacity 0.5s ease; }
input[type="text"]:focus:-ms-input-placeholder,
input[type="password"]:focus:-ms-input-placeholder {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease;
  -o-transition:	opacity 0.5s ease;
  transition:	opacity 0.5s ease; }



