.row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
    @media only screen and (max-width: 1024px) {
        display: block;
        margin: 0; } }

.col {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    padding: 0 10px;
    @media only screen and (max-width: 1024px) {
        display: block;
        width: 100%;
        padding: 0; } }

button {
    min-width: 140px;
    height: 31px;
    line-height: 31px;
    color: #fff;
    font-weight: 600;
    font-size: 16px;
    border-radius: 7px;
    background: #55bc56;
    cursor: pointer;
    text-decoration: none;
    border: none;
    padding: 0 15px;
    transition: .25s;
    &:hover {
        background: #1a6eb5; }

    &._large {
        height: 40px;
        line-height: 40px; }

    &._rect {
        border-radius: 0; }

    &._gray {
        background: #b1b0b0;
        &:hover {
            background: #1a6eb5; } }

    &._blue {
        background: #1a6eb5;
        &:hover {
            background: #55bc56; } }

    &:disabled {
        background: #b1b0b0 !important;
        cursor: default !important; } }


.custom-text {
    font-size: 16px;
    line-height: 1.25;

    &._typography {
        padding: 40px 0; }

    h1, h2, h3, h4, h5, h6 {
        margin: 1.25em 0 .75em;
        &:first-child {
            margin-top: 0; }
        &:last-child {
            margin-bottom: 0; } }

    h1 {
        font-size: 48px;
        @media only screen and (max-width: 1024px) {
            font-size: 36px; } }
    h2 {
        font-size: 40px;
        @media only screen and (max-width: 1024px) {
            font-size: 32px; } }
    h3 {
        font-size: 30px;
        @media only screen and (max-width: 1024px) {
            font-size: 28px; } }
    h4 {
        font-size: 25px;
        @media only screen and (max-width: 1024px) {
            font-size: 24px; } }
    h5 {
        font-size: 20px;
        @media only screen and (max-width: 1024px) {
            font-size: 20px; } }
    h6 {
        font-size: 18px;
        @media only screen and (max-width: 1024px) {
            font-size: 16px; } }

    p {
        color: inherit;
        margin: 1.3em 0;
        &:first-child {
            margin-top: 0; }
        &:last-child {
            margin-bottom: 0; } }

    a {
        color: inherit;
        text-decoration: underline;

        &:hover {
            color: #4b94d2;
            text-decoration: none; } }

    blockquote {
        position: relative;
        margin: 1.5em 0;
        font-weight: bold;
        line-height: 1.5;
        padding: 0 0 0 50px;

        &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            width: 30px;
            height: 30px;
            background: url(../img/quote.svg) top center/contain no-repeat; } }

    ol {
        margin: 10px 0;
        padding: 0;
        counter-reset: myCounter;

        >li {
            position: relative;
            display: flex;
            align-items: center;
            padding: 0 0 0 45px;
            margin: 0 0 10px;
            min-height: 30px;

            &:last-child {
                margin-bottom: 0; }

            &:before {
                counter-increment: myCounter;
                content: counter(myCounter);
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 30px;
                height: 30px;
                border: 2px solid #6bbd55;
                line-height: 24px;
                text-align: center; } } }
    ul {
        margin: 10px 0;
        padding: 0;
        list-style-type: none;

        >li {
            position: relative;
            padding: 0 0 0 25px;
            margin: 0 0 10px;

            &:last-child {
                margin-bottom: 0; }

            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 4px;
                top: 8px;
                width: 4px;
                height: 4px;
                background: #6bbd55; } } }

    pre {
        display: block;
        padding: 9.5px;
        font-size: 14px;
        word-break: break-all;
        word-wrap: break-word;
        background-color: #f5f5f5;
        border: 1px solid #ccc;
        border-radius: 4px;
        white-space: normal; }

    mark {
        background: #4b94d2;
        color: #fff;
        padding: 3px 6px; }

    table {
        width: 100%;
        text-align: left;
        border-collapse: collapse;
        &._border {
            border: 1px solid #ddd; } }

    thead {
        background: #4b94d2;
        color: #fff;
        font-weight: normal;
        font-size: 14px; }

    th {
        padding: 5px 10px; }

    td {
        padding: 5px 10px; }

    tr {
        &:nth-child(even) {
            background: #fff; } }

    .fl {
        &:first-letter {
            position: relative;
            font-size: 40px;
            line-height: 1;
            font-weight: bold;
            color: #4b94d2;
            float: left;
            margin: 0 15px 0 0; } }

    .flbg {
        &:first-letter {
            position: relative;
            float: left;
            font-size: 36px;
            padding: 5px 10px;
            text-align: center;
            font-weight: bold;
            background: #4b94d2;
            border-radius: 3px;
            color: #fff;
            margin: 0 10px 5px 0; } } }
