.cabinet {
  width: 100%;
  padding: 45px 0 0;
  &__container {
    height: auto;
    @include container();
    justify-content: space-between;
    align-items: stretch;
    @include bp(800px) {
      flex-direction: column; } }
  &__main,
  &__aside {
    width: calc((100% - 20px)/2);
    @include bp(800px) {
      width: 100%; } }
  &__tit {
    padding-bottom: 20px;
    padding-left: 25px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(30px);
    color: black; }
  &__block {
    margin-bottom: 35px;
    padding: 25px;
    background: white;
    border: 1px solid #d1d1d1; }
  &__form {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    input[type="text"],
    input[type="password"] {
      width: 355px;
      height: 40px;
      padding-left: 13px;
      @include fz(16px);
      @include lh(40px);
      color: $darkGrey;
      background: white;
      border: 1px solid $mainAccent;
      @include bp(1250px) {
        width: 100%; }
      + input[type="text"],
      + input[type="password"] {
        margin-top: 25px; } }
    .btn-blue {
      margin-top: 25px;
      width: 355px;
      @include bp(1250px) {
        width: 100%; }
      @include bp(480px) {
        padding: 0 15px; }
      &.disable {
        pointer-events: none;
        background: #bababa; } }
    &--mail {
      &.success {
        .cabinet__confirmed {
          display: block; } } }
    &--change {
      &.nonconfirm {
        .cabinet__confirmed {
          display: block; } } } }
  &__confirmed {
    display: none;
    position: absolute;
    left: 375px;
    vertical-align: middle;
    @include bp(1250px) {
      position: relative;
      top: 10px;
      left: 50%;
      transform: translateX(-50%); }
    &--red {
      top: 130px;
      @include bp(1250px) {
        top: 10px; }
      .cabinet__icon {
        color: $red; }
      .cabinet__subj {
        color: $red; } } }
  &__icon {
    @include fz(20px);
    @include lh(40px);
    color: $green; }
  &__subj {
    display: inline-block;
    padding-left: 5px;
    @include fz(16px);
    @include lh(40px);
    color: $green; }
  &__checkbox {
    width: 100%;
    display: block;
    cursor: pointer;
    position: relative;
    padding-left: 37px;
    padding-bottom: 15px;
    font-family: $lightFont;
    color: $darkGrey;
    transition: all .3s ease;
    @include fz(16px);
    @include lh(30px);
    &:hover,
    &:focus {
      color: $hrefColor; }
    &:before,
    &:after {
      position: absolute;
      display: block;
      content: '';
      width: 30px;
      height: 30px;
      background: white;
      border: 1px solid $blue;
      top: 0;
      left: 0; }
    &:after {
      transition: all .3s ease;
      display: none;
      background: white url(../img/check-blue.png) center no-repeat; } }
  input[type="checkbox"] {
    display: none;
    &:checked + .cabinet__checkbox:after {
      display: block; } }
  &__setting {
    .btn-blue {
      margin-top: 0; } }
  &__actions {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    .cabinet__block {
      min-height: 575px; } }

  &__topline,
  &__head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  &__topline {
    @include bp(600px) {
      flex-direction: column-reverse;
      align-items: flex-start; } }
  &__head {
    padding-bottom: 20px; }
  &__history {
    padding-bottom: 20px;
    font-family: $mainFont;
    @include fz(16px);
    @include lh(30px);
    color: $mainAccent;
    transition: all .3s ease;
    @include bp(600px) {
      padding-left: 25px; }
    &:hover,
    &:active,
    &:focus {
      color: $hrefColor; } }
  &__date {
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(24px);
    color: black; }
  &__time {
    font-family: $mainFont;
    @include fz(14px);
    @include lh(24px);
    color: $darkGrey; }
  &__text {
    padding-bottom: 40px;
    font-family: $mainFont;
    @include fz(14px);
    @include lh(24px);
    color: black; } }
.attent-text {
  margin-top: 25px;
  &--red {
    background: #ffd6d6; }
  &__icon {
    color: $red; } }
