.uslugi {
  width: 100%;
  padding: 50px 0 70px;
  &__container {
    @include container();
    align-items: flex-start; }
  &__block {
    width: calc((100% - 20px)/2);
    padding: 30px 25px;
    background: white;
    border: 1px solid #d1d1d1;
    &--main {
      @include bp(1250px) {
        width: 60%; }
      @include bp(1024px) {
        width: 100%;
        margin-bottom: 20px; } }
    &--attention {
      @include bp(1250px) {
        width: calc(40% - 20px); }
      @include bp(1024px) {
        width: 100%; } } }
  &__item {
    position: relative;
    padding: 0 15px 15px; }
  &__link {
    position: relative;
    font-family: $mainFont;
    @include fz(14px);
    @include lh(24px);
    color: $formBlue;
    transition: all .3s ease;
    z-index: 1;
    &:hover,
    &:active,
    &:focus {
      color: $blue; } }
  &__icon {
    @include fz(24px);
    @include lh(24px);
    color: $green;
    margin-left: 20px;
    cursor: pointer;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:focus {
      color: $blue; } } }

[data-toolip] {
  position: relative;
  vertical-align: middle;
  &:hover {
    .tooltip-message {
      visibility: visible;
      opacity: 1;
      margin-left: 25px; } } }

.tooltip-message {
  position: absolute;
  display: block;
  left: 100%;
  top: 50%;
  max-width: 100vw;
  width: 485px;
  padding: 20px;
  background: #fff;
  color: #213b4e;
  font-size: 13px;
  line-height: normal;
  font-family: 'OpenSansRegular', Arial, sans-serif;
  box-shadow: 0 0 3px rgba(0, 0, 0, .4);
  transform: translate(0, -50%);
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  margin-left: 35px;
  transition: .25s;
  &:before {
    position: absolute;
    left: -12px;
    top: 60px;
    display: block;
    content: '';
    width: 25px;
    height: 25px;
    transform: rotate(135deg);
    background: white;
    box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, .36); } }

.tooltip {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  max-width: 490px;
  padding: 20px;
  top: -30px;
  right: -95%;
  background: white;
  box-shadow: 3px 4px 8px 0 rgba(0, 0, 0, .36);
  z-index: 150;
  transition: all .3s ease;
  &.active {
    opacity: 1;
    visibility: visible; }
  @include bp(1250px) {
    max-width: 350px;
    right: -300px; }
  @include bp(1024px) {
    top: 50px;
    left: 0;
    right: auto; }
  &:before {
    position: absolute;
    left: -12px;
    top: 30px;
    display: block;
    content: '';
    width: 25px;
    height: 25px;
    transform: rotate(135deg);
    background: white;
    box-shadow: 2px 2px 2px -2px rgba(0, 0, 0, .36);
    @include bp(1024px) {
      top: -12px;
      left: 50px;
      transform: rotate(-135deg); } }
  &__message {
    font-family: $mainFont;
    @include fz(13px);
    color: $darkGrey; } }
.btn-blue {
  display: block;
  font-family: $mainFont;
  height: 40px;
  padding: 0 75px;
  @include fz(16px);
  @include lh(40px);
  color: white;
  background: $blue;
  border: none;
  transition: all .3s ease;
  @include bp(1024px) {
    margin: 0 auto; }
  @include bp(480px) {
    padding: 0 15px; }
  &:hover,
  &:active,
  &:focus {
    background: $green; } }
.attent-text {
  &--nb {
    margin-top: 0;
    margin-bottom: 20px; } }
