.history {
  width: 100%;
  padding: 50px 0 75px;
  @include bp(1250px) {
    padding: 35px 0 40px; }
  &__container {
    @include container();
    align-items: flex-start;
    @include bp(1250px) {
      flex-direction: column-reverse; } }
  &__main {
    width: 75%;
    max-width: 880px;
    @include bp(1250px) {
      width: 100%;
      max-width: 100%; } }
  &__tit {
    margin-bottom: 20px;
    padding: 0 30px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(30px);
    color: black;
    &--acide {
      text-align: center;
      padding: 0; } }
  &__filter {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    @include bp(800px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start; } }
  &__props {
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    border: 1px solid #d1d1d1; }
  &__prop {
    background: white;
    padding: 12px 25px;
    font-family: $mainFont;
    @include fz(13px);
    @include lh(13px);
    color: $darkGrey;
    transition: all .3s ease;
    cursor: pointer;
    @include bp(600px) {
      padding: 12px 20px; }
    @include bp(480px) {
      @include fz(11px);
      padding: 5px 10px; }
    + .history__prop {
      border-left: 1px solid #d1d1d1;
      &.active {
        border-left: 1px solid $blue; } }
    &.active {
      color: white;
      background: $blue;
      box-shadow: 0 0 1px 2px $blue; }
    &:not(.active):hover,
    &:not(.active):focus,
    &:not(.active):active {
      background: $inputBlue; } }
  &__months,
  &__years {
    margin: 0;
    padding: 9px 25px;
    font-family: $mainFont;
    @include fz(13px);
    @include lh(13px);
    color: $darkGrey;
    border: 1px solid #d1d1d1;
    @include bp(400px) {
      padding: 5px 20px; } }
  &__months {
    margin-right: -7px; }
  &__results {
    width: 100%;
    padding: 25px;
    background: white;
    border: 1px solid #d1d1d1;
    @include bp(600px) {
      padding: 15px; } }
  &__result {
    width: 100%;
    min-height: 120px;
    padding: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    + .history__result {
      border-top: 1px solid #d1d1d1; }
    @include bp(600px) {
      flex-wrap: wrap; } }
  &__message {
    width: 70%;
    max-width: 555px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    @include bp(600px) {
      width: 100%; } }
  &__type {
    flex-shrink: 0;
    display: block;
    width: 40px;
    height: auto;
    padding: 5px 10px 0 0;
    &--payment {
      padding: 0 10px 0 0; } }
  &__icon {
    display: block;
    width: 100%;
    height: auto;
    object-fit: contain; }
  &__content {
    padding-right: 20px; }
  &__date {
    margin-bottom: 20px;
    font-family: $twoBold;
    @include fz(25px);
    @include lh(25px);
    color: black; }
  &__text {
    font-family: $mainFont;
    @include fz(14px);
    @include lh(24px);
    color: $darkGrey; }
  &__aside {
    width: 25%;
    max-width: 280px;
    margin-top: 49px;
    padding: 30px 24px 25px;
    background: white;
    border: 1px solid #d1d1d1;
    @include bp(1250px) {
      width: 100%;
      max-width: 100%;
      padding: 20px 25px;
      margin-bottom: 20px; }
    @include bp(1024px) {
      margin-top: 0; } }
  &__attach {
    width: 100%;
    max-width: 275px;
    @include bp(800px) {
      max-width: 200px; }
    @include bp(600px) {
      max-width: 275px;
      padding-left: 40px; } }
  &__time {
    margin-bottom: 20px;
    text-align: right;
    font-family: $mainFont;
    @include fz(14px);
    @include lh(24px);
    color: $darkGrey;
    @include bp(600px) {
      margin-top: 20px;
      text-align: left; } }
  &__action {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    background: #ededed;
    @include bp(800px) {
      padding: 10px 15px; } }
  &__link {
    display: block;
    padding-left: 35px;
    text-transform: uppercase;
    font-family: $mainFont;
    @include fz(14px);
    @include lh(32px);
    color: $mainAccent;
    transition: all .3s ease;
    @include bp(800px) {
      @include fz(12px);
      padding-left: 25px; }
    &:hover,
    &:active,
    &:focus {
      color: $green; }
    + .history__link {
      margin-left: 15px;
      @include bp(800px) {
        margin-left: 10px; } }
    &--html {
      background: url(../img/icon--html.png) left center no-repeat;
      background-size: 25px auto;
      @include bp(800px) {
        background-size: 15px auto; } }
    &--pdf,
    &--print {
      background: url(../img/icon--pdf.png) left center no-repeat;
      background-size: 25px auto;
      @include bp(800px) {
        background-size: 15px auto; } }
    &--xml {
      background: url(../img/icon--xml.png) left center no-repeat;
      background-size: 25px auto;
      @include bp(800px) {
        background-size: 15px auto; } }
    &--repeat {
      background: url(../img/icon--circle.png) left center no-repeat;
      background-size: 25px auto;
      @include bp(800px) {
        background-size: 15px auto; } } }
  &__count {
    margin-bottom: 30px;
    text-align: center;
    font-family: $cBoldFont;
    @include fz(55px);
    color: #3b464f; } }
.attent-text {
  &--acide {
    margin-top: 40px; } }
.btn-blue {
  &--small {
    padding: 0 55px;
    @include bp(1250px) {
      margin: 20px auto; } } }
