.service {
  width: 100%;
  padding: 20px 0 0;
  border-bottom: 10px solid $green;
  &__container {
    @include container(); }
  &__item,
  &__hover {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 25%;
    max-width: 280px;
    margin-bottom: 20px;
    font-family: $semiFont;
    transition: .35s;
    @include fz(20px);
    @include lh(30px);
    color: white;
    @include bp(1250px) {
      max-width: 230px; }
    @include bp(1024px) {
      @include fz(14px);
      @include lh(20px);
      max-width: 180px; }
    @include bp(800px) {
      max-width: 130px;
      overflow: hidden; }
    @include bp(600px) {
      width: calc(50% - 10px);
      max-width: 100%;
      min-height: 144px;
      @include fz(16px);
      @include lh(24px); }
    @include bp(480px) {
      width: calc(50% - 5px);
      margin-bottom: 10px; }
    &--01 {
      width: 50%;
      max-width: 580px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      background: url(../img/service-01.png) center no-repeat;
      background-size: cover;
      @include bp(1250px) {
        max-width: 485px; }
      @include bp(1024px) {
        max-width: 375px; }
      @include bp(800px) {
        max-width: 280px; }
      @include bp(600px) {
        width: 100%;
        max-width: 100%; } }
    &--03 {
      background: url(../img/service-03.png) center no-repeat;
      background-size: cover; }
    &--04 {
      background: url(../img/service-04.png) center no-repeat;
      background-size: cover; }
    &--05 {
      background: url(../img/service-05.png) center no-repeat;
      background-size: cover; }
    &--06 {
      background: url(../img/service-06.png) center no-repeat;
      background-size: cover; }
    &--07 {
      background: url(../img/service-07.png) center no-repeat;
      background-size: cover; }
    &--08 {
      transition: all .3s ease;
      &:hover,
      &:active,
      &:focus {
        .service__hover {
          opacity: 1; } } } }
  &__img {
    width: 100%;
    height: auto;
    object-fit: cover;
    &--01 {
      width: 48%; }
    @include bp(800px) {
      height: 100%; } }

  &__hover {
    margin-bottom: 0;
    opacity: 0;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url(../img/service-08.png) center no-repeat;
    background-size: cover; }
  &__text {
    padding: 20px;
    color: white;
    @include bp(800px) {
      padding: 15px; } } }
