.hero {
  width: 100%;
  background: url(../img/hero__bcg.jpg) center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  &__container {
    padding: 198px 25px 23px;
    @include container();
    justify-content: stretch;
    align-items: flex-end;
    @include bp(1250px) {
      padding-top: 150px; }
    @include bp(800px) {
      padding-top: 80px; }
    @include bp(600px) {
      padding-top: 120px; }
    @include bp(480px) {
      padding: 120px 0 20px; } }
  &__descr {
    width: 50%;
    max-width: 600px;
    min-width: 300px;
    @include bp(1024px) {
      width: 80%;
      margin: 0 auto 20px;
      max-width: auto; }
    @include bp(800px) {
      width: 100%; } }
  &__title {
    padding-left: 28px;
    font-family: $boldFont;
    @include fz(36px);
    color: white;
    @include bp(600px) {
      @include fz(32px); }
    @include bp(480px) {
      margin: 15px auto 25px;
      padding-left: 5px;
      @include fz(24px); }
    &--small {
      padding-left: 0;
      @include fz(32px);
      @include bp(600px) {
        @include fz(24px); }
      @include bp(480px) {
        @include fz(20px); } } }
  &__steps {
    width: 100%;
    margin-top: 50px;
    padding: 25px 32px 34px;
    background: $darkGreyOp;
    @include bp(1250px) {
      padding: 25px 20px; }
    @include bp(800px) {
      margin-top: 30px; }
    @include bp(600px) {
      padding: 20px 15px; }
    @include bp(480px) {
      margin-top: 15px;
      padding: 15px 5px; } }
  &__tit {
    margin-bottom: 30px;
    font-family: $cBoldFont;
    @include fz(25px);
    @include lh(25px);
    text-align: center;
    color: white;
    @include bp(600px) {
      @include fz(20px); }
    @include bp(480px) {
      margin-bottom: 10px; } }
  &__step {
    margin-top: 15px;
    font-family: $lightFont;
    @include fz(16px);
    @include lh(30px);
    color: white;
    @include bp(600px) {
      @include fz(14px); }
    @include bp(480px) {
      @include fz(11px);
      @include lh(20px); } }
  &__num {
    display: inline-block;
    width: 34px;
    margin-right: 23px;
    text-align: center;
    @include fz(20px);
    @include lh(30px);
    border: 2px solid #6bbd55;
    @include bp(1250px) {
      margin-right: 20px; }
    @include bp(600px) {
      @include fz(18px); }
    @include bp(480px) {
      @include fz(14px);
      @include lh(20px);
      width: 20px;
      margin-right: 10px;
      border: 1px solid #6bbd55; } }
  &__content {
    background: $formBlue; } }
.tabs {
  flex-shrink: 0;
  width: 50%;
  min-width: 300px;
  @include bp(1024px) {
    width: 80%;
    margin: 0 auto; }
  @include bp(800px) {
    width: 100%; }
  &__controls {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  &__tab {
    width: calc(50% - 10px);
    text-align: center;
    font-family: $regularFont;
    @include fz(20px);
    @include lh(46px);
    color: $darkGrey;
    background: white;
    cursor: pointer;
    transition: all .5s ease;
    @include bp(600px) {
      @include fz(18px); }
    @include bp(480px) {
      @include fz(14px); }
    &.active {
      color: white;
      background: $formBlue; } }
  &__form {
    display: none;
    height: 317px;
    opacity: 0;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 16px 30px 0;
    background: $formBlue;
    transition: opasity .5s ease;
    @include bp(600px) {
      padding: 10px 15px 0; }
    @include bp(480px) {
      padding: 5px 10px 0; }
    &.active {
      display: flex;
      opacity: 1; }
    &--kadastr {
      padding: 30px 0 0;
      .polit {
        padding: 0 30px;
        @include bp(480px) {
          padding: 0 5px; } }
      @include bp(480px) {
        padding: 10px 0 0; } } }
  &__inputs {
    width: 100%; }
  input[type="text"] {
    height: 40px;
    margin-bottom: 18px;
    padding: 0 15px;
    @include fz(16px);
    @include lh(40px);
    border: none;
    background: $inputBlue; }
  &__inputNumbers {
    width: 100%;
    padding: 0 30px;
    @include bp(480px) {
      padding: 0 5px; } }
  &__sity,
  &__street {
    width: 100%; }
  &__house,
  &__room {
    width: calc(50% - 10px); }
  &__room {
    margin-left: 20px; }
  &__kNumber {
    width: 100%; }
  &__example {
    display: inline-block;
    width: 55%;
    text-align: center;
    @include fz(16px);
    @include lh(30px);
    color: white;
    @include bp(1250px) {
      text-align: left;
      width: 50%;
      @include fz(14px); }
    @include bp(600px) {
      display: none; } }
  &__knowhow {
    display: inline-block;
    width: 45%;
    text-align: center;
    @include fz(13px);
    @include lh(30px);
    color: white;
    transition: all .3s ease;
    &:hover,
    &:focus,
    &:active {
      color: $mainFontColor; }
    @include bp(1250px) {
      width: 50%;
      text-align: right;
      @include fz(12px); }
    @include bp(600px) {
      display: none; } }
  &__attention {
    width: 100%;
    text-align: center;
    background: $red; }
  &__text {
    font-family: $mainFont;
    @include fz(16px);
    @include lh(60px);
    color: white;
    @include bp(1250px) {
      @include fz(14px); }
    @include bp(600px) {
      @include fz(12px);
      @include lh(40px); }
    @include bp(480px) {
      @include lh(30px); } }
  &__accent {
    font-family: $boldFont; }
  &__btn {
    flex-shrink: 0;
    width: 255px;
    height: 40px;
    margin: 20px 0;
    font-family: $mainFont;
    @include fz(20px);
    @include lh(40px);
    color: white;
    border: none;
    background: $blue;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:hover {
      background: $green; } } }

.polit {
  &__checkbox {
    width: 100%;
    display: block;
    cursor: pointer;
    position: relative;
    padding-left: 37px;
    font-family: $mainFont;
    color: $inputBlue;
    transition: all .3s ease;
    @include fz(12px);
    @include bp(480px) {
      @include fz(10px); }
    &:hover,
    &:focus {
      color: white; }
    &:before,
    &:after {
      position: absolute;
      display: block;
      content: '';
      width: 20px;
      height: 20px;
      background: $blue;
      top: 0;
      left: 0; }
    &:after {
      transition: all .3s ease;
      display: none;
      background: $blue url(../img/check.png) center no-repeat; } }
  input[type="checkbox"] {
    display: none;
    &:checked + .polit__checkbox:after {
      display: block; } } }
