.heroK {
  width: 100%;
  padding: 95px 0 65px;
  background: url(../img/hero-kadastr__bcg.jpg) top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  @include bp(1250px) {
    padding: 70px 0 65px; }
  @include bp(1024px) {
    padding-top: 80px; }
  @include bp(800px) {
    padding-top: 40px; }
  @include bp(600px) {
    padding-top: 120px;
    padding-bottom: 40px; }
  &__container {
    @include container();
    flex-direction: column; }
  &__main {
    width: 100%;
    padding: 55px 35px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    background: #ededed;
    @include bp(1250px) {
      padding: 35px 35px 0; }
    @include bp(800px) {
      margin-top: 20px;
      padding: 25px 15px 0; }
    @include bp(600px) {
      margin-top: 0; }
    @include bp(480px) {
      padding: 25px 5px 0; } }
  &__title {
    flex-shrink: 0;
    width: 100%;
    margin-bottom: 35px;
    font-family: $cBoldFont;
    color: $darkGrey;
    @include fz(40px);
    @include bp(600px) {
      @include fz(34px);
      margin-bottom: 60px; } }
  &__wrapinfo {
    position: relative;
    width: 50%;
    margin-bottom: 40px;
    padding-top: 20px;
    background: white;
    @include bp(800px) {
      width: 70%;
      margin-bottom: 20px; }
    @include bp(600px) {
      width: 100%; }
    .btn {
      text-align: center;
      margin-left: 15px;
      width: 245px;
      height: 43px;
      font-family: $regularFont;
      @include fz(20px);
      @include lh(43px);
      transition: all .3s ease;
      color: white;
      @include bp(480px) {
        margin-left: 0; }
      &--blue {
        background: $blue;
        @include bp(480px) {
          margin: 0 auto; } } }
    .label--price {
      position: absolute;
      top: 10px;
      right: -150px;
      width: 156px;
      height: 73px;
      padding-left: 25px;
      display: block;
      text-align: center;
      color: $darkGrey;
      font-family: $cBoldFont;
      @include fz(35px);
      @include lh(50px);
      background: url(../img/label.png) center no-repeat;
      background-size: contain;
      z-index: 3;
      @include bp(600px) {
        top: -60px;
        right: 0; }
      &:after {
        content: '₽';
        margin-left: -5px;
        @include fz(30px); } } }

  .tabs {
    width: 100%;
    &__controls {
      width: 100%;
      padding: 0 15px;
      &--price,
      &--ergn {
        @include bp(480px) {
          padding: 0; }
        .tabs__tab {
          @include bp(1024px) {
            width: 46%;
            @include fz(14px); } } } }
    &__tab {
      border-radius: 10px;
      text-align: center;
      width: 245px;
      height: 43px;
      font-family: $regularFont;
      @include fz(20px);
      @include lh(43px);
      color: $darkGrey;
      transition: all .3s ease;
      border: 1px solid #d1d1d1;
      &.active,
      &:hover,
      &:focus,
      &:active {
        border: 1px solid transparent;
        color: white;
        background: $blue; } }
    &__form {
      padding: 34px 15px 15px;
      background: white;
      @include bp(1024px) {
        height: auto;
        min-height: 350px; }
      @include bp(480px) {
        padding: 10px 0; }
      input[type="text"] {
        height: 35px;
        margin-bottom: 18px;
        padding: 0 15px;
        @include fz(16px);
        @include lh(35px);
        border: 1px solid $mainAccent;
        background: transparent; }
      .btn {
        font-family: $semiFont;
        width: 280px;
        height: 40px;
        @include fz(16px);
        @include lh(40px);
        border-radius: 0;
        @include bp(480px) {
          width: 100%;

          @include fz(14px); } }
      &--kadastr {
        padding: 0;
        .polit {
          padding: 0 15px;
          @include bp(480px) {
            padding: 0; } } } }
    &__inputNumbers {
      padding: 35px 15px 0;
      @include bp(1024px) {
        padding-top: 20px; }
      @include bp(480px) {
        padding: 20px 5px 0; } }
    &__example,
    &__knowhow {
      width: 50%;
      margin-bottom: 18px;
      color: $darkGrey;
      @include bp(1024px) {
        width: 100%; }
      @include bp(600px) {
        display: block; } }
    &__example {
      text-align: left;
      @include bp(1024px) {
        margin-bottom: 0;
        text-align: center; } }
    &__knowhow {
      text-align: right;
      @include bp(1024px) {
        margin-bottom: 10px;
        text-align: center; }
      &:hover,
      &:active,
      &:focus {
        color: $hrefColor; } }
    &__attention {
      .tabs__text {
        @include bp(1024px) {
          @include fz(12px);
          @include lh(30px); }
        @include bp(480px) {
          @include fz(16px); } } } }
  .polit {
    &__checkbox {
      color: $darkGrey;
      &:hover,
      &:focus {
        color: $hrefColor; } } }
  &__descr {
    width: calc(50% - 20px);
    margin-left: 20px;
    padding: 95px 0 25px 20px;
    @include bp(1250px) {
      padding: 70px 0 20px 15px; }
    @include bp(800px) {
      width: 100%;
      padding: 0 0 20px;
      margin-left: 0; }
    &--ergn {
      padding-top: 0;
      .twocol {
        @include bp(1024px) {
          flex-direction: column; } }
      .dashlist {
        &--ergn {
          @include bp(1024px) {
            width: 100%; } } } } }
  &__tit {
    position: relative;
    margin-bottom: 20px;
    font-family: $cBoldFont;
    @include fz(25px);
    color: $darkGrey;
    &--big {
      @include fz(30px); }
    &--verybig {
      @include fz(40px); }
    &:hover {
      .tips {
        opacity: 1;
        visibility: visible; } } }
  &__icon {
    color: $orange;
    cursor: pointer; }
  &__text {
    margin-bottom: 15px;
    font-family: $mainFont;
    @include fz(16px);
    @include lh(24px);
    color: $darkGrey;
    @include bp(1250px) {
      @include lh(22px);
      margin-bottom: 10px; }
    &--white {
      @include fz(18px);
      @include lh(24px);
      color: white; } }
  &__accent {
    font-family: $boldFont;
    font-weight: normal; }
  &__dopinfo {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    padding: 55px 20px 65px 35px;
    background: white;
    @include bp(1250px) {
      padding: 35px; }
    @include bp(800px) {
      padding: 20px; }
    &--ergn {
      @include bp(800px) {
        flex-direction: column; }
      .twocol {
        @include bp(1024px) {
          flex-direction: column; } }
      .dashlist {
        &--ergn {
          @include bp(1024px) {
            width: 100%; } } } } }
  &__question {
    width: 65%;
    max-width: 725px;
    @include bp(1250px) {
      width: 50%; }
    @include bp(1024px) {
      width: 100%; }
    &--ergn {
      width: 45%;
      @include bp(800px) {
        width: 100%; }
      .twocol {
        margin-top: 20px; } } }
  &__notice {
    position: relative;
    display: block;
    width: 34%;
    min-height: 260px;
    padding: 65px 30px 20px 40px;
    background: url(../img/notice.png) top left no-repeat;
    background-size: 372px 255px;
    @include bp(1250px) {
      width: 45%;
      padding-bottom: 25px; }
    @include bp(1024px) {
      background-size: 300px 255px;
      padding: 40px 20px 30px 30px; }
    @include bp(1024px) {
      width: 100%;
      min-height: 200px;
      padding: 0;
      background: none; }
    @include bp(600px) {
      min-height: 0; }
    &:after {
      content: '';
      position: absolute;
      right: 10px;
      bottom: -139px;
      display: block;
      width: 165px;
      height: 256px;
      background: url(../img/man.svg) top center no-repeat;
      background-size: 165px auto;
      z-index: 10;
      @include bp(1250px) {
        bottom: -171px;
        width: 190px;
        height: 290px;
        background-size: 190px auto; }
      @include bp(1024px) {
        right: -25px;
        bottom: -35px;
        width: 180px;
        height: 250px;
        background-size: 100% auto; }
      @include bp(800px) {
        right: 0;
        bottom: -20px;
        width: 150px;
        height: 240px;
        background-size: 100% auto; }
      @include bp(600px) {
        display: none; } }
    &--price {
      padding-top: 30px;
      @include bp(1250px) {
        padding-left: 20px;
        padding-right: 50px; }
      @include bp(1024px) {
        padding: 20px 0 0;
        min-height: auto; }
      .heroK__text {
        @include fz(14px);
        @include bp(1024px) {
          @include fz(18px); } }
      &:after {
        bottom: -162px;
        @include bp(1250px) {
          bottom: -215px; }
        @include bp(1024px) {
          display: none; } } }
    &--erng {
      width: 50%;
      padding: 65px 90px 20px 40px;
      background: url(../img/notice.png) top left no-repeat;
      background-size: 555px 255px;
      @include bp(1250px) {
        padding: 60px 60px 0 30px;
        background-size: 450px 250px; }
      @include bp(1024px) {
        padding: 10px;
        background: none; }
      @include bp(800px) {
        width: 100%;
        min-height: 0; }
      &:after {
        bottom: -135px;
        @include bp(1250px) {
          bottom: -171px;
          width: 190px;
          height: 290px;
          background-size: 190px auto; }
        @include bp(1024px) {
          bottom: -233px; }
        @include bp(800px) {
          display: none; } } }
    .heroK__text--white {
      @include bp(1024px) {
        color: $darkGrey; }
      br {
        @include bp(600px) {
          display: none; } } } } }
.tips {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: 0;
  top: 50px;
  padding: 25px;
  background: white;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .36);
  z-index: 20;
  transition: all .3s ease;
  &:before {
    content: '';
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    background: white;
    top: -15px;
    right: 153px;
    transform: rotate(45deg);
    z-index: -1; }
  &__text {
    max-width: 500px;
    font-family: $lightFont;
    @include fz(15px);
    color: $darkGrey; } }
//breadcrumbs
.breadcrumbs {
  position: relative;
  display: flex;
  margin-bottom: 25px;
  padding-left: 300px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  z-index: 10;
  @include bp(1250px) {
    padding-left: 260px; }
  @include bp(1024px) {
    padding-left: 30px;
    margin-bottom: 15px; }
  @include bp(800px) {
    padding-left: 170px; }
  @include bp(600px) {
    padding-left: 25px;
    margin-bottom: 10px; }
  @include bp(480px) {
    width: 95%;
    padding-left: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  &__crumb {
    font-family: $lightFont;
    color: white;
    @include fz(12px);
    @include lh(30px);
    transition: all .3s ease;
    &:last-child {
      pointer-events: none; }
    + .breadcrumbs__crumb {
      padding-left: 10px;
      &:before {
        padding-right: 10px;
        content: '\\';
        color: white;
        &:hover,
        &:active,
        &:focus {
          color: white; } } }
    &:hover,
    &:active,
    &:focus {
      color: $hrefColor; } } }

.offer {
  width: 100%;
  margin-bottom: 15px;
  padding: 23px 35px 23px 120px;
  background: $orange;
  border: 2px dashed white;
  @include bp(600px) {
    padding: 20px;
    text-align: center; }
  @include bp(480px) {
    display: none; }
  &--center {
    padding: 25px;
    text-align: center;
    .offer__text {
      @include fz(35px);
      @include bp(1024px) {
        @include fz(20px); } } }
  &__text {
    font-family: $mainFont;
    @include fz(20px);
    @include lh(20px);
    color: white;
    @include bp(1250px) {
      @include fz(16px); }
    &--strong {
      font-family: $boldFont;
      font-weight: normal; } } }
.twocol {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start; }

.dashlist {
  &--ergn {
    display: inline-block;
    width: 45%;
    .dashlist__text {
      margin-bottom: 0;
      color: $darkGrey; } } }
