@mixin fz($size) {
  font-size: ($size / $mainFontSize) * 1rem; }

@mixin lh($size) {
  line-height: ($size / $mainFontSize) * 1rem; }

@mixin bp($point) {
  @media only screen and (max-width:$point) {
    @content; } }


%container {
  width: 100%;
  max-width: 1230px;
  padding: 0 25px;
  margin: 0 auto;
  display: flex;
  @include bp(1250px) {
    padding: 0 15px;
    max-width: 1024px; }
  @include bp(1024px) {
    max-width: 800px; }
  @include bp(800px) {
    padding: 0 10px;
    max-width: 600px; }
  @include bp(600px) {
    max-width: 480px; }
  @include bp(480px) {
    padding: 0 5px;
    max-width: 320px; } }

@mixin container($align:space-between, $wrap:wrap) {
  @extend %container;
  justify-content: $align;
  flex-wrap: $wrap; }
