.footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  min-height: 422px;
  padding-top: 68px;
  background: url(../img/footer__bcg.png) center top/cover no-repeat;
  @include bp(600px) {
    padding-top: 40px; }
  &__container {
    @include container(flex-start);
    @include bp(800px) {
      align-items: flex-start; }
    @include bp(480px) {
      flex-direction: column; } }
  &__nav {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    @include bp(800px) {
      width: 50%;
      margin-bottom: 20px; }
    @include bp(600px) {
      width: 100%; }
    &--main {
      margin-right: 100px;
      @include bp(1024px) {
        margin-right: 30px; }
      @include bp(800px) {
        margin-right: 0;
        order: 0; } }
    &--middle {
      margin-right: 154px;
      padding-top: 75px;
      @include bp(1024px) {
        margin-right: 30px; }
      @include bp(800px) {
        margin-right: 0;
        margin-top: -100px;
        padding-top: 0;
        order: 2; }
      @include bp(600px) {
        margin-top: 20px; }
      @include bp(480px) {
        order: 1;
        margin-top: 10px; } }
    &--service {
      @include bp(480px) {
        order: 2; } } }
  &__logo {
    margin-bottom: 21px;
    @include bp(600px) {
      width: 150px;
      height: auto; } }
  &__img {
    @include bp(600px) {
      width: 100%;
      height: auto;
      object-fit: contain; } }
  &__title {
    font-family: $boldFont;
    text-transform: uppercase;
    color: white;
    @include fz(18px);
    @include lh(75px);
    @include bp(600px) {
      @include lh(58px); } }
  &__link {
    font-family: $lightFont;
    @include fz(13px);
    @include lh(30px);
    color: white;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:focus {
      color: $mainFontColor; } }
  .bottomLine {
    width: 100%;
    background: $darkGrey;
    &__container {
      height: 60px;
      @include container();
      align-items: center; }
    &__copyright,
    &__view,
    &__support,
    &__mail {
      font-family: $lightFont;
      @include fz(15px);
      @include lh(30px);
      color: #fff; }
    &__view {
      margin-left: 180px;
      padding-left: 34px;
      background: url(../img/eye.png) center left no-repeat;
      @include bp(1250px) {
        margin-left: 0; }
      @include bp(600px) {
        display: none; } }
    a {
      transition: all .3s ease;
      &:hover,
      &:active,
      &:focus {
        color: $green; } } } }
