.heroLK {
  width: 100%;
  min-height: 245px;
  padding-top: 195px;
  padding-bottom: 25px;
  background: white;
  @include bp(1250px) {
    padding-top: 150px;
    padding-bottom: 10px; }
  @include bp(1025px) {
    min-height: 200px;
    padding-bottom: 20px; }
  @include bp(480px) {
    padding-top: 180px; }
  &__container {
    @include container(); }
  &__title {
    font-family: $boldFont;
    @include fz(36px);
    color: #1a6fb6;
    @include bp(480px) {
      @include fz(30px);
      padding-left: 20px; } } }
