.LK {
  .header {
    &__logo {
      background: #ededed; }
    &__nav {
      background: $green;
      a {
        border-left: 1px solid #ededed;
        color: white;
        @include bp(1250px) {
          padding: 0 10px; }
        @include bp(481px) {
          border-bottom: 1px solid #ededed; }
        &:hover,
        &:active,
        &:focus {
          color: $inputBlue; } } }
    &__cabinet {
      max-width: 250px;
      padding: 0 20px 0 60px;
      background: url(../img/key--green.png) left 40px center no-repeat;
      @include bp(1250px) {
        padding: 0 20px 0 30px;
        background: none; }
      @include bp(800px) {
        padding: 0 10px;
        max-width: 136px; }
      @include bp(600px) {
        max-width: 160px;
        width: 33.33%; }
      &:hover,
      &:active,
      &:focus {
        background: url(../img/key.png) left 40px center no-repeat;
        @include bp(1250px) {
          background: none; } } }
    &__data,
    &__order,
    &__history,
    &__package {
      text-align: center;
      padding: 0 20px;
      @include bp(800px) {
        padding: 0 10px; } }
    &__data,
    &__order {
      @include bp(600px) {
        width: 33.33%; } }
    &__history,
    &__package {
      @include bp(600px) {
        width: 50%; } }
    &__enter {
      position: relative;
      top: -35px;
      width: 80%;
      max-width: 900px;
      min-height: 35px;
      margin-left: 280px;
      padding: 17px 25px 0 40px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      @include bp(1250px) {
        top: -50px;
        padding-left: 0; }
      @include bp(1024px) {
        top: 0;
        margin-left: 25px;
        width: 100%; }
      @include bp(800px) {
        top: -50px;
        margin: 0 0 0 auto;
        width: 70%; }
      @include bp(600px) {
        width: 65%; }
      @include bp(480px) {
        top: -10px;
        width: 100%;
        margin: 0 auto 0 25px; } }
    &__balance {
      font-family: $boldFont;
      @include fz(16px);
      @include lh(30px);
      color: $darkGrey; }
    &__count {
      font-family: $lightFont; }
    &__logout {
      padding-right: 22px;
      position: relative;
      font-family: $lightFont;
      @include fz(16px);
      @include lh(30px);
      color: $darkGrey;
      transition: all .3s ease;
      &:after {
        position: absolute;
        bottom: 0;
        right: 0;
        content: '';
        display: block;
        width: 17px;
        height: 30px;
        background: url(../img/logout.png) center no-repeat;
        transition: all .3s ease; }
      &:hover,
      &:active,
      &:focus {
        color: $hrefColor;
        &:after {
          background: url(../img/logout--grey.png) center no-repeat; } } } } }
