.faq {
  width: 100%;
  padding-top: 110px;
  background: url(../img/faq__bcg.jpg) top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  @include bp(600px) {
    padding-bottom: 25px; }
  &__container {
    @include container(flex-start);
    flex-direction: column; }
  &__title {
    text-align: center;
    font-family: $cBoldFont;
    @include fz(40px);
    color: #fefefe;
    @include bp(600px) {
      @include fz(34px); } }
  &__main {
    width: 100%;
    margin-top: 55px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    @include bp(600px) {
      flex-wrap: wrap;
      margin-top: 30px; } }
  &__wrap {
    width: 32%;
    @include bp(1024px) {
      width: 45%; }
    @include bp(600px) {
      width: 100%; }
    &--center {
      margin-left: 25px;
      background: url(../img/faq__center.png) center no-repeat;
      background-size: contain;
      @include bp(1024px) {
        display: none; } } }
  &__item {
    &--tar {
      text-align: right; } }
  &__tit {
    margin-bottom: 10px;
    font-family: $boldFont;
    text-transform: uppercase;
    @include fz(20px);
    color: white; }
  &__text {
    margin-bottom: 40px;
    font-family: $lightFont;
    @include fz(14px);
    @include lh(22px);
    color: white; }
  &__bottom {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(600px) {
      flex-wrap: wrap; } }
  &__left,
  &__right {
    width: 45%;
    @include bp(600px) {
      width: 100%; } } }

.dashlist {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  list-style-type: none;
  &__text {
    position: relative;
    margin-bottom: 30px;
    font-family: $lightFont;
    @include fz(14px);
    @include lh(22px);
    color: white;
    &--dark {
      color: $darkGrey; }
    &:before {
      position: absolute;
      content: '—';
      left: -20px; } }
  &--bottom {
    li {
      margin-bottom: 0; } } }
