.story {
  width: 100%;
  padding: 120px 0 60px;
  background: url(../img/story__bcg.jpg) center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  &__container {
    @include container();
    flex-direction: column;
    align-items: flex-start; }
  &__title {
    padding-left: 30px;
    font-family: $boldFont;
    @include fz(36px);
    color: white;
    @include bp(1024px) {
      margin-bottom: 20px; }
    @include bp(800px) {
      padding-left: 20px; }
    @include bp(600px) {
      @include fz(34px); } }
  &__textwrap {
    width: 100%;
    margin-bottom: 50px;
    padding-left: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    @include bp(800px) {
      flex-wrap: wrap;
      padding-left: 0; }
    &--price {
      padding-top: 35px; } }
  &__column {
    width: calc((100% - 20px)/2);
    padding: 35px 35px 35px 0;
    @include bp(1024px) {
      padding: 20px 20px 20px 0; }
    @include bp(800px) {
      width: 100%;
      padding: 20px; }
    &--border {
      padding: 35px;
      border-radius: 10px;
      border: 2px dotted white;
      @include bp(1024px) {
        padding: 20px; } } }
  &__text {
    padding-bottom: 15px;
    font-family: $mainFont;
    @include fz(18px);
    @include lh(28px);
    color: white;
    + .story__text--strong {
      display: block;
      margin-top: 15px;
      padding-top: 15px;
      border-top: 1px solid white; } }
  .btn {
    padding: 0 20px;
    height: 65px;
    margin: 0 auto;
    font-family: $semiFont;
    @include fz(26px);
    @include lh(65px);
    @include bp(600px) {
      @include fz(20px);
      @include lh(40px); }
    @include bp(480px) {
      @include fz(16px); } } }
