.article {
  width: 100%;
  padding: 60px 0 65px;
  background: url(../img/hero-kadastr__bcg.jpg) top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  @include bp(480px) {
    padding: 40px 0 35px; }
  &__container {
    @include container(); }
  &__content {
    background: white;
    padding: 50px 30px 20px;
    @include bp(480px) {
      padding: 20px 10px; } }
  &__title {
    margin-bottom: 35px;
    font-family: $cBoldFont;
    @include fz(35px);
    @include lh(30px);
    color: $darkGrey;
    @include bp(800px) {
      @include lh(42px);
      margin-bottom: 25px; }
    @include bp(800px) {
      @include lh(42px);
      margin-bottom: 25px; }
    @include bp(600px) {
      @include fz(30px);
      @include lh(40px); }
    @include bp(480px) {
      @include fz(28px);
      @include lh(40px); } }
  &__text {
    margin-bottom: 25px;
    font-family: $mainFont;
    @include fz(16px);
    @include lh(24px);
    color: $darkGrey;
    &--strong {
      font-family: $boldFont;
      font-weight: normal; }
    &--white {
      color: white; } }
  &__link {
    font-family: $boldFont;
    font-weight: normal;
    text-decoration: underline;
    color: $darkGrey;
    transition: all .3s ease;
    &:hover,
    &:active,
    &:focus {
      text-decoration: none; } }
  &__special {
    margin-bottom: 25px;
    padding: 35px 35px 10px;
    background: $formBlue;
    @include bp(800px) {
      padding: 20px; }
    @include bp(600px) {
      padding-bottom: 10px; } } }
